const getIconForStatus = (status) => {
  let icon;
  switch (status) {
    case 'Not Started':
      icon = 'circle';
      break;
    case 'In Progress':
      icon = 'star-circle';
      break;
    case 'Skipped':
      icon = 'x-circle-f';
      break;
    case 'Complete':
      icon = 'check-circle-f';
      break;
  }
  return icon;
};

